.grid-container {
  --columns: 1;
  grid-template-columns: repeat(var(--columns), 200px);
  justify-content: center;
  align-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .grid-container {
    --columns: 2;
  }
}

@media (width >= 768px) {
  .grid-container {
    --columns: 3;
  }
}

@media (width >= 1024px) {
  .grid-container {
    --columns: 4;
  }
}

p, body {
  margin: 0;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  height: 408px;
  transition: transform .3s;
  display: flex;
}

.card__img {
  width: 160px;
  height: 134px;
  margin-top: 32px;
  display: block;
}

.card__name {
  width: 166px;
  margin-top: 40px;
  margin-bottom: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: name .3s;
}

.card__code {
  color: #616070;
  align-self: flex-start;
  width: 98px;
  margin-top: 4px;
  margin-left: 16px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__review {
  justify-content: space-between;
  align-items: center;
  width: 166px;
  height: 16px;
  margin-top: 16px;
  display: flex;
}

.card__stars {
  align-items: center;
  width: 96px;
  height: 16px;
  display: flex;
}

.card__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.card__star:not(:last-child) {
  margin-right: 4px;
}

.card__stars .card__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.card__number {
  height: 16px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  line-height: 16px;
}

.card__price {
  justify-content: space-between;
  width: 166px;
  margin-top: 24px;
  display: flex;
}

.card__price-label {
  color: #616070;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-value {
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy {
  color: #fff;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin-top: 16px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  transition: background-color .3s, color .3s, border-color .3s;
  display: flex;
}

.card__buy:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover__name {
  color: #34568b;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

:root {
  --blue: #00acdc;
  --header-height: 60px;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding-inline: 50px;
  display: flex;
}

.nav {
  text-transform: uppercase;
}

.header__logo {
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  line-height: var(--header-height);
  color: #060b35;
  margin-right: 20px;
  text-decoration: none;
  transition: color .3s;
  display: block;
  position: relative;
}

.nav__link:hover {
  color: var(--blue);
}

.nav__item:last-child .nav__link {
  margin-right: 0;
}

.nav__link.is-active {
  color: var(--blue);
}

.nav__link.is-active:after {
  content: "";
  background: var(--blue);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
